export default {
	props:['video'],
	methods:{
		openVideoPage() {
			let page = getCurrentPages();
			let currentPage = page[0].__route__
			let isMine = false
			if (currentPage === "pages/mine/mine") {
				isMine = true
			}
			uni.redirectTo({
				url:'../../pages/video/videoSingle?videoItem=' + JSON.stringify(this.video) + '&isMine=' + isMine
			})
		}
	}
}
